.product {
  width: 300px;
  height: fit-content;
  background: white;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.product .imgbox {
  height: 100%;
  box-sizing: border-box;
}
.product .imgbox img {
  display: block;
  width: 90%;
  margin: 20px auto 0;
}
.details {
  width: 90%;
  background: white;
  color: black;
  padding: 10px;
  box-shadow: 0 0 0 rgba(0, 0, 0, 0);
  transition: 0.5s;
  display: flex;
  flex-direction: column;
  position: relative;
}
.product:hover .details {
  bottom: 0;
  box-shadow: 0 -5px 15px rgba(0, 0, 0, 0.25);
}
.details h1 {
  margin: 0;
  padding: 0;
  font-size: 18px;
  width: 100%;
}
.details h1 span {
  font-size: 14px;
  color: rgb(141, 137, 137);
  font-weight: normal;
}
.details .price {
  position: absolute;
  right: 5px;
  font-weight: bold;
  color: #04456f;
  font-size: 20px;
}
.details .price strike {
  font-weight: normal;
  color: #cdd0d1;
  font-size: 16px;
}
label {
  display: block;
  margin-top: 5px;
  font-weight: bold;
  font-size: 16px;
}
a {
  display: block;
  padding: 5px;
  color: #fff;
  margin: 15px 0;
  background: #04456f;
  text-align: center;
  text-decoration: none;
  transition: 0.5s;
  cursor: pointer;
}
a:hover {
  color: #fff;
  background: #00c8ff;
}
